body {
  font-family: 'TT Lakes Condensed Thin';
  padding: 0px;
  margin: 0px; }

@media screen and (min-width: 1120px) {
  body {
    width: 1120px;
    margin: 0 auto; } }

@media screen and (min-width: 768px) {
  body {
    width: 768px;
    margin: 0 auto; } }

@media screen and (min-width: 320px) {
  body {
    width: 320px;
    margin: 0 auto; } }

.set-too {
  -webkit-animation-name: bl-too;
          animation-name: bl-too;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

.set {
  -webkit-animation-name: bl;
          animation-name: bl;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards; }

@-webkit-keyframes bl {
  to {
    -webkit-filter: blur(30px);
            filter: blur(30px); } }

@keyframes bl {
  to {
    -webkit-filter: blur(30px);
            filter: blur(30px); } }

@-webkit-keyframes bl-too {
  from {
    -webkit-filter: blur(30px);
            filter: blur(30px); }
  to {
    -webkit-filter: blur(0px);
            filter: blur(0px); } }

@keyframes bl-too {
  from {
    -webkit-filter: blur(30px);
            filter: blur(30px); }
  to {
    -webkit-filter: blur(0px);
            filter: blur(0px); } }
